.paranetSkill{
    width: 100%;
    height: 600px;
    /* border: 1px solid black; */
    background-color: #eee;
    /* margin-top: 100px; */
}
.childSkill-1{
   
    color: #DC143C;
    /* color:#000; */
    /* border: 1px solid #DC143C; */
    margin: 0px auto;
    font-size: 40px;
    font-family: Ubuntu,"sans-sarif";
}
.childSkill-2{
    display: grid;
    grid-template-columns: repeat(5,1fr);
    justify-content: space-evenly;
    /* margin-top: 100px; */
    margin: 30px 30px;
    padding: 0px 20px;
    /* border: 1px solid #DC143C; */
   
    
  
}
.box{
    /* border:1px solid red; */
    text-align:left;
    margin-bottom: 10px;
    width:60%;
    height:130px;
    /* border: 1px solid #DC143C; */

}
.box h1{
    font-size: 25px;
    color: #000;
    font-family: Poppins,"sans-sarif";

}

@media (min-width: 300px) and (max-width: 700px) {
    .paranetSkill{ 
       height: auto;
    }
    .childSkill-2{
     
        grid-template-columns: repeat(1,1fr);
       justify-content: center;
    }
    .childSkill-2 div{
        /* margin-left: 150px; */
        margin: 0px 120px;
        /* overflow-x: hidden; */
    }
    .box{
        margin-top: 50px;
        width: 50%;
        /* overflow-x: hidden; */
        
    }
}

@media (min-width: 600px) and (max-width: 900px) {
    .paranetSkill{
        height: 900px;
    }
    .childSkill-2{
      
        grid-template-columns: repeat(3,1fr);
      
    }
}
